body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.box{
  text-transform:uppercase;
}
.tempbox{
margin-top:2rem;
text-align: center;
}
.card{
  box-shadow: 0px 1px 5px rgb(159, 230, 253);
  margin:1rem 0rem;
}
.heading{
  background: -webkit-linear-gradient(rgb(13, 147, 180), rgb(165, 12, 179));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 2rem;
  margin-bottom: 1rem;
  font-weight: 500;
}
.nav{
  background: linear-gradient(rgb(188, 228, 238), rgb(211, 197, 212));
}